@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  min-height: 1000px;
  font-family: 'Montserrat', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}
.nav_main {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 100px;
    z-index: 10;
    transition: .6s;
}

.nav_main.sticky {
    padding: 0px 100px;
    background: #fff;
    box-shadow: 1px 1px 50px #000;
}

.logo_main_container {
    margin: 20px 0;
    background: linear-gradient(89deg, #CFD6E6 0%, #E7EFF9  100%);
}

.nav_main.sticky .logo_main_container {
    margin: 10px 0;
}

.logo_main {
    position: relative;
    padding: 0;
    margin: 0;
    height: 100px;
}

.ul_menu_main {
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
}

.li_menu_main_item {
    position: relative;
    list-style-type: none;
    transition: .5s;
    padding: 40px 0;
}

/* .li_menu_main_item:after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    content: '.';
    color: transparent;
    background: #000;
    height: 1px;
} */

.li_menu_main_item:hover:after {
    width: calc(100%);
}

.link_main_nav.active {
    background: #000;
    border-radius: 10px;
    padding: 20px;
    margin: 0;
    text-transform: uppercase;
    box-sizing: border-box;
    opacity: 1;
}

.link_main_nav.active span {
    color: #fff;
}

.link_main_nav:hover {
    opacity: 1;
}

.link_main_nav {
    position: relative;
    background: none;
    border: none;
    margin: 0;
    padding: 20px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 700;
    transition: .6s;
    cursor: pointer;
    opacity: .6;
}

.link_main_nav span {
    margin: 0;
    padding: 0;
}

.link_main_nav:after {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);  
    transition: -webkit-transform 250ms ease-in-out;  
    transition: transform 250ms ease-in-out;  
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}

.link_main_nav:hover:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.nav_main.sticky .link_main_nav {
    color: #000;
}

.nav_main_mobile {
    display: none;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #fff;
    -webkit-clip-path: circle(25px at calc(100% - 35px) 35px);
            clip-path: circle(25px at calc(100% - 35px) 35px);
    transition: all .5s ease-in-out;
    z-index: 10;
}

.nav_main_mobile.active {
    -webkit-clip-path: circle(75%);
            clip-path: circle(75%);
}

.hamburger_container {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    cursor: pointer;
}

.hamburger_container span {
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    width: 50%;
    height: 2px;
    background: #000;
    transition: all .5s ease-in-out;
}

.hamburger_container span:first-child {
    top: 30%;
}

.hamburger_container span:nth-child(2) {
    top: 50%;
}

.hamburger_container span:last-child {
    top: 70%;
}

.nav_main_mobile.active .hamburger_container span:first-child {
    top: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}

.nav_main_mobile.active .hamburger_container span:nth-child(2) {
    display: none;
}

.nav_main_mobile.active .hamburger_container span:last-child {
    top: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}

.mobile_links-container {
    position: absolute;
    display: none;
    top: 50%;
    left: 50%;
    padding: 3vh 0;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
}

.nav_main_mobile.active .mobile_links-container {
    display: block;
}

.link_main_mobile_nav {
    position: relative;
    display: block;
    margin: 3vh 0;
    padding: 2vh 0;
    font-size: 4vh;
    font-weight: 600;
    text-decoration: none;
    color: #000;
    text-transform: uppercase;
    letter-spacing: .5vh;
    cursor: pointer;
}

.link_main_mobile_nav:after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    height: 2px;
    width: 0%;
    background: #000;
    transition: all .5s ease-in-out;
}

.link_main_mobile_nav:hover:after {
    width: 100%;
}

.ul_social-media_mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    z-index: 5;
    padding: 2vh 0;
    margin: 0;
}

.ul_social-media_mobile li {
    background: #fff;
    border-radius: 10px;
    margin: 0;
    display: flex;
    align-items: center;
    transition: .5s;
    opacity: 1;
}

.ul_social-media_mobile li a {
    height: 10vh;
    width: auto;
}

.icon_social-media_mobile {
    height: 8vh;
    width: auto;
    margin: 0 2vw;
}

.icon_social-media_mobile:hover {
    cursor: pointer;
}

@media only screen and (max-width: 1200px) {
    .nav_main {
        display: none;
    }

    .nav_main_mobile {
        display: block;
    }
}
.welcome-page_container {
    position: relative;
    width: 100%;
    min-height: 100vh;

    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: #051937;
    background-size: contain;
    background-repeat: no-repeat;
}

.particles {
    z-index: 4;
    height: 100vh;
}

.description_container {
    width: 50%;
    margin: 220px 0 0 30px;
    padding: 0 20px 40px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header_welcome {
    color: #fff;
    text-align: center;
    font-size: 1.75vw;
    line-height: 3vw;
    margin: 0;
}

.header_welcome span {
    color: #27d4cf;
    font-size: 1.8em;
}

.welcome-description_container {
    border: 1px solid #fff;
    padding: 1.5vw;
    margin: 1vw 0;
    border-radius: 25px;
}

.p_welcome-description {
    color: #fff;
    padding: 0;
    margin: 0;
    text-align: center;
    font-size: 1.5vw;
    line-height: 1.5vw;
}

.p_quote {
    color: #27d4cf;
    text-align: center;
    padding: 0;
    margin: 0;
    font-size: 1.5vw;
    line-height: 1.5vw;
    text-transform: uppercase;
    margin-top: 1vw;
    padding-top: 1vw;
    border-top: 1px solid #fff;
}

.link_about {
    position: relative;
    background: none;
    border: none;
    margin: 0;
    padding: 1vw;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-size: 1.5vw;
    font-weight: 300;
    transition: .5s;
    cursor: pointer;
    z-index: 5;
}

.link_about:hover {
    background: #fff;
    color: #000;
    border-radius: 10px;
}

.link_about span {
    margin: 0;
    padding: 0;
}

.profile-picture_container {
    margin-top: 220px;
    padding: 0;
    z-index: 5;
}

.img_profile-picture {
    height: 35vw;
    width: auto;
    margin-bottom: 40px;
    border-radius: 25px;
    opacity: .75;
}

.ul_social-media {
    list-style-type: none;
    z-index: 5;
    padding: 0 20px 40px 20px;
    margin: 220px 0 0 0;
}

.ul_social-media li {
    background: #fff;
    border-radius: 10px;
    margin: .8vw 0;
    display: flex;
    align-items: center;
    transition: .5s;
    opacity: .45;
}

.ul_social-media:hover li {
    opacity: .85;
}

.ul_social-media li a {
    height: 2.8vw;
    width: 3vw;
}

.icon_social-media_container {
    background: linear-gradient(89deg, #CFD6E6 0%, #E7EFF9  100%);
}

.icon_social-media {
    height: 2.2vw;
    width: auto;
    margin: .3vw .4vw .1vw .4vw;
}

.icon_social-media:hover {
    cursor: pointer;
}

@media only screen and (max-width: 1200px) {
    .welcome-page_container {
        min-height: calc(100vh + 10vw);
        padding: 0 0 10vw;

        flex-direction: column-reverse;
        justify-content: space-around;
    }

    .description_container {
        width: 100%;
        margin: 0;
    }

    .header_welcome {
        font-size: 5vw;
        line-height: 7vw;
        margin: 0 0 1vw;
    }
    
    .header_welcome span {
        font-size: 7vw;
    }
    
    .welcome-description_container {
        padding: 3vw;
        margin: 2vh 0 2vh;
    }
    
    .p_welcome-description {
        font-size: 3vw;
        line-height: 3vw;
    }
    
    .p_quote {
        font-size: 3vw;
        line-height: 3vw;
        margin-top: 2vw;
        padding-top: 2vw;
    }
    
    .link_about {
        margin: 1vw 0 0;
        padding: 3vw;
        background: #fff;
        color: #000;
        border-radius: 25px;
        font-size: 4vw;
    }
    
    .link_about:hover {
        background: #000;
        color: #fff;
    }

    .profile-picture_container {
        margin: 20px 0;
    }
    
    .img_profile-picture {
        margin: 0;
        width: 60vw;
        height: auto;
        border-radius: 50px;
    }
    
    .ul_social-media {
        display: none;
    }
}
.about-page_container {
    position: relative;
    width: 100%;
    min-height: calc(100vh + 5px);
    padding-top: 140px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image:
        linear-gradient(to bottom, rgba(5, 25, 55, 1), rgba(33, 66, 116, 0.5)),
        url(/resume/static/media/background-about.ff802ca5.jpg);

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    z-index: 5;
}

.about-page_inside_container {
    width: 90%;
    position: relative;
    display: flex;
    justify-content: space-between;
}

.about-page_left_container {
    width: 35%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.about-page_right_container {
    width: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-page_card_container {
    position: relative;
}

.about-page_left_container .face {
    width: 14vw;
    height: 14vw;
    transition: 0.5s;
}

.about-page_right_container .face {
    width: 55vw;
    height: 14vw;
    transition: 0.5s;
}

.face.face1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 6;
    transition: 1s;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    background: rgba(5, 25, 55, .5);
}

.about-page_card_container:hover .face.face1 {
    background: #000;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.content {
    opacity: .6;
    transition: .5s;
    text-align: center;
}

.img_about-page_card {
    max-width: 8vw;
    -webkit-filter: invert(1);
            filter: invert(1);
}

.content h3 {
    margin: 1vw 0 0;
    padding: 0;
    color: #fff;
    text-align: center;
    font-size: 1.5vw;
}

.about-page_card_container:hover .content {
    opacity: 1;
}

.face.face2 .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-page_card_container:hover .img_about-page_card {
    max-width: 8vw;
}

.face.face2 {
    position: relative;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: .5vw;
    box-sizing: border-box;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
    opacity: 0;
}

.about-page_card_container:hover .face.face2 {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}

.face.face2 p {
    margin: 0;
    padding: 0;
}

.personal-info {
    margin: 0;
    padding: 0;
    font-size: 1vw;
    line-height: 1.8vw;
    line-break: auto;    
    vertical-align: middle;
    display: inline-block;
}

.personal-info_name {
    transition: ease-in 1s;
    opacity: 0;
}

.personal-info_birthdate {
    transition: ease-in 2s;
    opacity: 0;
}

.personal-info_birthplace {
    transition: ease-in 2.5s;
    opacity: 0;
}

.about-page_personal-info_container:hover .personal-info_name, 
.about-page_personal-info_container:hover .personal-info_birthdate,
.about-page_personal-info_container:hover .personal-info_birthplace {
    opacity: 1;
}

.personal-info span {
    font-size: 1.5em;
    color: #27d4cf;
    font-weight: 700;
    text-transform: uppercase;
}

.education-name {
    margin: 0;
    padding: 0;
    padding-bottom: .5vw;
    font-size: 1vw;
    line-height: 1.5vw;
    line-break: auto;
    transition: ease-in 1s;
    font-weight: 500;
    vertical-align: middle;
    display: inline-block;
    border-bottom: 1px solid #000;
    opacity: 0;
}

.education-name span {
    color: #27d4cf;
    font-weight: 700;
}

.education-info {
    width: 100%;
    margin: 0;
    padding: .5vw 0;
    font-size: 1vw;
    line-height: 1.5vw;
    line-break: auto;
    transition: ease-in 2s;
    vertical-align: middle;
    display: inline-block;
    border-bottom: 1px solid #000;
    opacity: 0;
}

.education-info span {
    color: #27d4cf;
    font-weight: 700;
}

.education-time {
    width: 100%;
    margin: 0;
    padding: .5vw 0;
    font-size: .8vw;
    line-height: 1.5vw;
    line-break: auto;
    transition: ease-in 2.5s;
    vertical-align: middle;
    display: inline-block;
    opacity: 0;
}

.education-time span {
    color: #27d4cf;
    font-size: 1vw;
    font-weight: 700;
}

.about-page_education_container:hover .education-name,
.about-page_education_container:hover .education-info,
.about-page_education_container:hover .education-time {
    opacity: 1;
}

.hobbies_flexbox {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hobbies-column {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border-right: 1px solid #000;
}

.hobbies-column:last-child {
    border: none;
}

.hobby-title {
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hobby-title p {
    font-size: 1.7vw;
    font-weight: 700;
    transition: 1s;
    opacity: .35;
}

.hobbies-column:hover .hobby-title p {
    color: #27d4cf;
    opacity: 1;
}

.hobby-description {
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.3vw;
    padding: 0 .25vw;
}

.hobby-description p {
    transition: 1s;
    opacity: .35;
    text-align: center;
}

.hobbies-column:hover .hobby-description p {
    opacity: 1;
}

@media only screen and (max-width: 1200px) {
    .about-page_container {
        padding: 40px 0;
        flex-direction: column;
    }  
    
    .about-page_inside_container {
        min-height: 100%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }

    .about-page_left_container {
