        font-size: 3.7vw;
        line-height: 4.3vw;
    }

    .personal-info_name {
        opacity: 1;
    }
    
    .personal-info_birthdate {
        opacity: 1;
    }
    
    .personal-info_birthplace {
        opacity: 1;
    }
    
    .personal-info span {
        font-size: 4.1vw;
    }

    .personal-info_content,
    .education_content {
        padding: 2vw;
    }

    .education-name {
        padding-bottom: 1.4vw;
        font-size: 2.7vw;
        line-height: 3vw;
        opacity: 1;
    }

    .education-name span {
        font-size: 3vw;
    }
    
    .education-info {
        padding: 1.4vw 0;
        font-size: 2.7vw;
        line-height: 3vw;
        opacity: 1;
    }

    .education-info span {
        font-size: 3vw;
    }
    
    .education-time {
        padding: 1.4vw 0;
        font-size: 2.7vw;
        line-height: 3vw;
        opacity: 1;
    }

    .education-time span {
        font-size: 3vw;
    }

    .about-page_left_container .face {
        width: 45vw;
        height: 30vw;
    }

    .about-page_right_container .face {
        width: 90vw;
        height: 30vw;
    }

    .about-page_hobbies_container {
        width: 100%;
        display: flex;
        align-items: center;
    }
    
    .hobbies-column {
        padding: .25vw;
    }
    
    .hobby-title {
        height: 15%;
    }
    
    .hobby-title p {
        font-size: 3vw;
        opacity: 1;
    }
    
    .hobby-description {
        height: 85%;
        font-size: 2.5vw;
    }

    .hobby-description p {
        opacity: 1;
    }
}
.skills-page_container {
    position: relative;
    width: 100%;
    min-height: calc(100vh + 5px);

    padding-top: 140px;

    display: flex;
    justify-content: center;
    align-items: center;

    font-family: 'Montserrat', sans-serif;

    background-image:
        linear-gradient(to bottom, rgba(20, 38, 65, 1), rgba(33, 66, 116, 0.5)),
        url(/resume/static/media/background-skills.533e834a.png);

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    z-index: 5;
}

.skills-page_inside_container {
    position: relative;
    width: 90%;
